.login{
    display: grid;
    place-items: center;
    margin-left: auto;
    margin-right: auto;
    padding: 100px 0;
}
.login > img{
    object-fit: contain;
    height: 70px;
    margin: 20px 0;
}
.login > form{
    display: flex;
    flex-direction: column;
}
.login > form > input{
    width: 350px;
    height: 50px;
    font-size: 15px;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}
.login > form > button{
    width: 365px;
    height: 50px;
    color: white;
    background-color: #0074b1;
    font-size: large;
    border-radius: 5px;
}
.login__register{
    color: #0177b1;
    cursor: pointer;
}
.login > p{
    margin-top: 20px;
}