.sidebar{
    position:sticky;
    top:80px;
    flex: 0.2;
    border-radius: 10px;
    text-align:center;
    height: fit-content;
} 
.sidebar__stats{
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgrey;
    /* border-top-left-radius: 10px; */
    /* border-top-right-radius: 10px; */
    background-color: white;

}
.sidebar__top h4{
    color: grey;
    font-size: 12px;
}
.sidebar__top h2{
    font-size: 18px;
}
.sidebar__avatar{
    margin-bottom: 10px;
}
.sidebar__top{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgrey;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
}
.sidebar__top > img {
    object-fit: cover;
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.sidebar__stat{
    margin-top:10px;
    display: flex;
    justify-content: space-between;
}
.sidebar__stat >p {
    color: grey;
    font-size: 13px;
    font-weight: 600;
}
.sidebar__statNumber{
    font-weight: bold;
    color: #0a66c2 !important;
}
.sidebar__button{
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
}
.sidebar__recentItems{
    display: flex;
    color: grey;
    font-size: 13px;
    font-weight: bolder;
    cursor: pointer;
    padding: 5px;
}
.sidebar__button >p {
    font-size: 13px;
    padding-bottom: 10px;
}
.sidebar__recentItems:hover{
    border-radius: 10px;
    background-color: whitesmoke;
    cursor: pointer;
    color: black;
}
.sidebar__hash{
    margin-right: 10px;
    margin-left: 5px;
}